@charset "UTF-8";

* {
  box-sizing: border-box;
}

.foto_equipo {
  background-image: url("../images/bg-contacto.jpg");
  background-size: 170%;
  background-repeat: no-repeat;
  background-position: right bottom;
  height: 300px;
  background-color: #1D1C21;
}

.contacto_h2 {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: #099d8d;
}

.principal_contacto {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding-top: 30px;
}

.contacto-txt {
  margin: 15px 15px 0;
}

.contacto_p {
  text-align: center;
  margin: 0;
  color: #54585a;
  font-size: 18px;
  line-height: 24px;
  max-width: 442px;
}

.forms {
  flex-direction: column;
  display: flex;
  color: #54585a;
  width: 100%;
  max-width: 442px;
  padding: 0 15px;
}

.forms_info {
  text-align: center;
  font-size: 0.7em;
  font-weight: normal;
  margin: 30px 0 60px;
}

.forms_labels {
  text-align: left;
  font-size: 14px;
  margin: 0;
  line-height: 19px;
}

.bright {
  color: #14d9c4;
  font-size: 1rem;
}

.form1 {
  border: 1px solid lightgray;
  min-height: 15px;
  text-align: left;
  font-size: 16px;
  width: 100%;
  padding: 10px;
  margin: 5px 0 15px;
  line-height: 22px;
}

.big {
  min-height: 70px;
  max-height: 105px;
  margin-bottom: 25px;
}

::placeholder {
  color: #b8b8b9;
}

input[type=text],select {
  color: #54585a;
}

input[type=submit] {
  background: #099d8d;
  height: 45px;
  border-radius: 5px;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  margin-bottom: 60px;
  font-size: 16px;
  font-weight: 400;
}

.send_container {
  display: flex;
  justify-content: flex-end;
}

.button-send {
  text-align: right;
  width: 100%;
}

.dragonfly {
  position: fixed;
  bottom: 0;
  left: -230px;
  animation-name: dragonfly;
  animation-duration: 20s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.dragonfly2 {
  width: 20vw;
  min-width: 120px;
  max-width: 200px;
  position: fixed;
  top: 0;
  left: -320px;
  animation-name: dragonfly2;
  animation-duration: 20s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 30s;
}

.footer_bordertop {
  border-top: 1px solid lightgray;
}

.principal_footer {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto auto auto;
}

.footer {
  font-size: 12px;
  padding-top: 30px;
  color: lightgray;
  text-align: center;
}

.footer_dracarys {
  width: 20%;
  max-width: 90px;
}

.footer_menu_item {
  list-style: none;
  text-align: center;
  line-height: 24px;
  padding: 0;
  margin: 20px 0 30px;
}

.footer_link {
  text-decoration: none;
  font-size: 15px;
  color: #54585a;
  font-weight: 600;
  font-family: "Rubik", sans-serif;
  display: block;
}

.footer-img {
  text-align: center;
}

.footer_adalab {
  width: 115.9px;
  height: 40px;
  margin-bottom: 45px;
}

.outter {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.adjust-header-outter {
 /* Esta clase es para ajustar el encabezado
  el ancho y alto del box. */ /*height: 94vw;*/
  max-height: 600px;
}

.outter-header {
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: column;
  z-index: 1;
  justify-content: flex-end;
}

.menu-item {
  list-style: none;
  display: inline;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  list-style: none;
  margin: 0 15px;
  padding: 0;
}

.menu-item a {
  text-decoration: none;
  font-size: 15px;
  color: #ffffff;
  font-weight: 600;
  font-family: "Rubik", sans-serif;
}/* estos códigos son para producir un offset en los
