  height: 50px;
  width: auto;
  align-items: center;
}

.outter-header:hover {
  height: 145px;
}

.outter-header:hover .boxlogo {
  height: 100px;
}

.outter-header:hover img {
  transition: height ease 0.25s;
  height: 90px;
}

.boxlogo {
  height: 60px;
  width: auto;
  background: rgba(34, 160, 146, 0.95);
  justify-content: center;
  display: flex;
  align-items: center;
}

.box {
  width: 100%;
  height: 45px;
  background: rgba(57, 218, 200, 0.9);
}

.foto {
  background-image: url("../images/temp-main-img.jpeg");
  background-position: 0 40%;
  background-size: 120%;
  background-repeat: no-repeat;
  height: 421px;
  background-color: #1D1C21;
}

.title-main {
  text-align: center;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

.lema {
  height: 20px;
}

.box-lema {
  background: #ffffff;
  height: auto;
  width: auto;
  padding: 15px;
  position: relative;
  transform: translateY(-34px);
  margin: 0 15px;
}

.box-lema .equipo-txt h2 {
  margin-top: 35px;
}

.equipo-txt {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box-lema h1 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 25px;
  text-align: center;
}

.equipo-txt p {
  text-align: center;
  color: #54585a;
  font-size: 16px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 0;
}

.equipo-txt h3 {
  font-family: "Rubik", sans-serif;
  margin: 0;
  color: #099d8d;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  line-height: 45px;
}

.equipoparrafo {
  max-width: 601px;
}

.contenedor-triangle {
  display: flex;
  justify-content: center;
  margin: 30px 0 -30px;
}

.triangle {
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-top: 30px solid #ffffff;
  margin-bottom: 10px;
}

.about {
  background: #f1f1f1;
  padding: 30px 15px 45px;
}

.who {
  text-align: center;
}

.who .title-main {
  margin-bottom: 15px;
}

.photoprofile {
  border: 5px solid #b8b8b9;
  width: 180px;
  height: 180px;
  border-radius: 4px;
  margin: 0;
}

.member {
  padding: 0 15px;
}

.member h3 {
  margin: 0 0 15px;
  font-family: "Rubik", sans-serif;
  font-size: 20px;
  line-height: 45px;
}

.member p {
  font-family: 'Open Sans', sans-serif;
  color: #54585a;
  font-size: 16px;
  line-height: 24px;
}

.listaiconos {
  padding: 15px 0 45px;
  display: flex;
  justify-content: center;
  list-style: none;
  color: #099d8d;
  margin: 0;
}

.fyd-fortalezas {
  margin-bottom: 30px;
}

.fab,.fas {
  color: #099d8d;
}

.listaiconos li::before {
  content: " ∙ ";
  margin: 0 10px;
  padding-bottom: 45px;
}

.border-bottom {
  border-bottom: 1px solid #b8b8b9;
}

.listaiconos li:first-child::before {
  content: "";
}

body,html {
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.uppercase {
  text-transform: uppercase;
}

.principal {
  height: auto;
  display: flex;
  flex-direction: column;
}

.principal h2 {
  font-family: "Rubik", sans-serif;
  color: #099d8d;
  font-size: 24px;
  font-weight: bold;
  line-height: 45px;
  text-align: center;
  margin: 30px 0 15px;
}

.fab:hover,.fas:hover,a:hover {
  color: #FFD700;
}

